<template>
  <section class="step-3">
    <div class="add-place-des">
      <h4>Place Type</h4>
    </div>
    <ValidationObserver v-slot="{ invalid }">
      <form @submit.prevent="">
        <ValidationProvider
          name="placeType"
          rules="required"
          v-slot="{ errors }"
        >
          <div class="form-input-wrapper">
            <label class="check-container"
              >Apartment
              <input type="radio" checked="checked" v-model="placeType" value="1"/>
              <span class="checkmark"></span>
            </label>
          </div>

          <div class="form-input-wrapper">
            <label class="check-container"
              >Hotel Room
              <input type="radio" v-model="placeType" value="2"/>
              <span class="checkmark"></span>
            </label>
          </div>

          <div class="form-input-wrapper">
            <label class="check-container"
              >House
              <input type="radio" v-model="placeType" value="3"/>
              <span class="checkmark"></span>
            </label>
          </div>

          <span>{{ errors[0] }}</span>
        </ValidationProvider>

        <Navigation :valid="!invalid" />
      </form>
    </ValidationObserver>
  </section>
</template>

<script>
import Navigation from "./Navigation.vue";
import { ValidationObserver, ValidationProvider, extend } from "vee-validate";

extend("required", {
  validate(value) {
    return {
      required: true,
      valid: ["", " ", null, undefined].indexOf(value) === -1,
      message: "This field is required",
    };
  },
  computesRequired: true,
});

export default {
  components: { Navigation, ValidationObserver, ValidationProvider, extend },
  data() {
    return {};
  },
  computed: {
    placeType: {
      get() {
        return this.$store.state.steps.step3.placeType;
      },
      set(value) {
        this.$store.commit("setPlaceType", { placeType: value });
      },
    },
  },
  methods: {},
};
</script>

<style scoped lang="scss">
span {
  display: block;
}
/* Customize the label (the check-container) */
.check-container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 1.2rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: $text;
}

/* Hide the browser's default radio button */
.check-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom radio button */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  // background-color: #eee;
  border-radius: 50%;
  border: 1px solid $text;
}

/* On mouse-over, add a grey background color */
.check-container:hover input ~ .checkmark {
  // background-color: #ccc;
  border: 1px solid $textDark;
}

/* When the radio button is checked, add a blue background */
.check-container input:checked ~ .checkmark {
  background-color: $primary;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.check-container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.check-container .checkmark:after {
  top: 8px;
  left: 8px;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: white;
}
</style>
